.powered-by,.reviews-title{text-align:center}.carousel-container{position:relative;max-width:1000px;margin:0 auto;overflow:hidden}.review-card{background-color:#f3f4f6;padding:24px;border-radius:8px;box-shadow:0 4px 6px rgba(0,0,0,.1);transition:opacity .3s,transform .3s}.review-card.fade-out{opacity:0;transform:translateX(-20px)}.stars{color:#fbbf24;font-size:24px;margin-bottom:12px}.review-text{margin-bottom:16px}.author-info{display:flex;align-items:center}.author-initial{background-color:#065f46;color:#fff;width:40px;height:40px;border-radius:50%;display:flex;align-items:center;justify-content:center;font-weight:700;margin-right:12px}.author-name{font-weight:600}.review-date{color:#6b7280;font-size:14px}.google-logo{height:24px;margin-top:16px}.nav-button{position:absolute;top:50%;transform:translateY(-50%);background-color:#fff;border:none;border-radius:50%;width:40px;height:40px;font-size:24px;cursor:pointer;box-shadow:0 2px 4px rgba(0,0,0,.1)}.prev-button{left:-20px}.next-button{right:-20px}.powered-by{margin-bottom:10%}